function ClothesSize({ selectedSizes, handleSizeChange }) {
  return (
    <div>
      <ul className="size_select">
        <li>
          <input id="size_list_xs" type="checkbox" checked={selectedSizes.includes("XS")} onChange={() => handleSizeChange("XS")}></input>
          <label for="size_list_xs">XS</label>
        </li>
        <li>
          <input id="size_list_s" type="checkbox" checked={selectedSizes.includes("S")} onChange={() => handleSizeChange("S")}></input>
          <label for="size_list_s">S</label>
        </li>
        <li>
          <input id="size_list_m" type="checkbox" checked={selectedSizes.includes("M")} onChange={() => handleSizeChange("M")}></input>
          <label for="size_list_m">M</label>
        </li>
        <li>
          <input id="size_list_l" type="checkbox" checked={selectedSizes.includes("L")} onChange={() => handleSizeChange("L")}></input>
          <label for="size_list_l">L</label>
        </li>
        <li>
          <input id="size_list_xl" type="checkbox" checked={selectedSizes.includes("XL")} onChange={() => handleSizeChange("XL")}></input>
          <label for="size_list_xl">XL</label>
        </li>
      </ul>
    </div>
  );
}

export default ClothesSize;
