import React, { useState } from "react";
import SlideItems from "../component/Slideitems";

function Cart({ selectedSizes, selectedProducts }) {
  //도착 예정일 계산
  const today = new Date();
  const month = today.getMonth();
  const date = today.getDate();

  //로컬스토리지에서 배열이 삭제되도 렌더링이 되지 않기 때문에 useState를 사용해서 담고 선택된 값이 없다면 초기 값은 빈 배열
  const [cartItems, setCartItems] = useState(JSON.parse(localStorage.getItem("cartItems")) || []);

  //체크 박스
  const [checkedItems, setCheckedItems] = useState([]);

  const handleSelectedAll = () => {
    if (checkedItems.length === cartItems.selectedProducts.length) {
      setCheckedItems([]);
    } else {
      setCheckedItems(
        cartItems.selectedProducts.map((item) => ({
          id: item.id,
          size: item.size,
        }))
      );
    }
  };

  const handelSelectedSingle = (itemId, itemSize) => {
    if (checkedItems.some((item) => item.id === itemId && item.size === itemSize)) {
      setCheckedItems(checkedItems.filter((item) => item.id !== itemId || item.size !== itemSize));
    } else {
      setCheckedItems([...checkedItems, { id: itemId, size: itemSize }]);
    }
  };

  const handleDelete = (productId, size) => {
    //삭제하기에 선택된 id 값을 가진 상품을 제외하고 나머지 상품들을 저장하기위해 !==로 다른 값들을 찾음
    const updatedSelectedProducts = cartItems.selectedProducts.filter((product) => product.id !== productId || product.size !== size);
    //추가 된 상품 업데이트 기존의 배열은 남겨두기 위해 복사한 후 selectedProducts에 업데이트 된 값을 넘겨주기
    const updatedCartItems = {
      ...cartItems,
      selectedProducts: updatedSelectedProducts,
    };

    //state의 값을 위에서 변수로 선언한 updatedCartItems로 사용해서 기존에 있던 배열과 업데이트 된 배열을 합친다.
    setCartItems(updatedCartItems);
    localStorage.setItem("cartItems", JSON.stringify(updatedCartItems));
  };

  const handleCheckedDelete = () => {
    const updatedSelectedProducts = cartItems.selectedProducts.filter((product) => !checkedItems.some((item) => item.id === product.id && item.size === product.size));

    const updatedCartItems = {
      ...cartItems,
      selectedProducts: updatedSelectedProducts,
    };

    setCartItems(updatedCartItems);
    setCheckedItems([]);
    localStorage.setItem("cartItems", JSON.stringify(updatedCartItems));
  };

  const handleQuantityIncrease = (productId) => {
    const updatedSelectedProducts = cartItems.selectedProducts.map((product) => {
      if (product.id === productId && product.quantity < 10) {
        const updatedQuantity = product.quantity + 1;
        const updatedPrice = (parseInt(product.price.replace(/,/g, "")) / product.quantity) * updatedQuantity;
        return {
          ...product,
          quantity: updatedQuantity,
          price: updatedPrice.toLocaleString() + " 원",
        };
      }
      return product;
    });

    const updatedCartItems = {
      ...cartItems,
      selectedProducts: updatedSelectedProducts,
    };

    setCartItems(updatedCartItems);
    localStorage.setItem("cartItems", JSON.stringify(updatedCartItems));
  };

  const handleQuantityDecrease = (productId) => {
    const updatedSelectedProducts = cartItems.selectedProducts.map((product) => {
      if (product.id === productId && product.quantity > 1) {
        const updatedQuantity = product.quantity - 1;
        const updatedPrice = (parseInt(product.price.replace(/,/g, "")) / product.quantity) * updatedQuantity;
        return {
          ...product,
          quantity: updatedQuantity,
          price: updatedPrice.toLocaleString() + " 원",
        };
      }
      return product;
    });

    const updatedCartItems = {
      ...cartItems,
      selectedProducts: updatedSelectedProducts,
    };

    setCartItems(updatedCartItems);
    localStorage.setItem("cartItems", JSON.stringify(updatedCartItems));
  };

  //총 가격 계산
  const handleTotalPrice = () => {
    //총 가격 계산
    let total = 0;
    for (const product of cartItems.selectedProducts) {
      const price = parseInt(product.price.replace(/,/g, ""));
      total += price;
    }
    //10만원 이상이면 배송비 무료
    let arriveExpenseFree;
    if (total >= 100000) {
      arriveExpenseFree = "0";
    } else {
      arriveExpenseFree = "3000 원";
    }

    let priceDel = total;
    if (parseInt(arriveExpenseFree) !== 0) {
      priceDel += parseInt(arriveExpenseFree);
    }

    return { total, arriveExpenseFree, priceDel };
  };
  const { total, arriveExpenseFree, priceDel } = handleTotalPrice();

  return (
    <div className="cart">
      <div className="cart_wrapper">
        <div className="inner">
          <h2 className="cart_title">장바구니</h2>
          <section className="cart_order">
            {cartItems.selectedProducts.length === 0 ? (
              <div className="cart_list_none">장바구니에 상품이 없습니다</div>
            ) : (
              <div className="cart_list">
                <table>
                  <colgroup></colgroup>
                  <thead>
                    <tr>
                      <th className="cart_check">
                        <div>
                          <label for="all">전체</label>
                          <input id="all" type="checkbox" checked={checkedItems.length === cartItems.selectedProducts.length} onChange={handleSelectedAll} />
                        </div>
                      </th>
                      <th className="cart_img">상품 이미지</th>
                      <th className="cart_information">상품 / 옵션 정보</th>
                      <th className="cart_length">수량</th>
                      <th className="cart_price">주문금액</th>
                      <th className="cart_btn">삭제</th>
                    </tr>
                  </thead>
                  <tbody>
                    {cartItems.selectedProducts.map((product, i) => (
                      <tr key={product.id}>
                        <td className="cart_check">
                          <input
                            type="checkbox"
                            checked={checkedItems.some((item) => item.id === product.id && item.size === product.size)}
                            onChange={() => {
                              handelSelectedSingle(product.id, product.size);
                            }}
                          />
                        </td>
                        <td className="cart_img">
                          <div className="img_wrapper">
                            <img src={process.env.PUBLIC_URL + "/img/" + product.src} alt={product.name} />
                          </div>
                        </td>
                        <td className="cart_information">
                          <div>
                            <p>{product.name}</p>
                            <p>현재 컬러 : {product.color}</p>
                            <p>스타일 : {product.style}</p>
                            <p>사이즈 : {product.size}</p>
                          </div>
                        </td>
                        <td className="cart_length">
                          <div>
                            <button
                              type="button"
                              className="quantity_plus"
                              onClick={() => {
                                handleQuantityIncrease(product.id);
                              }}
                            >
                              +
                            </button>
                            <p>{product.quantity}</p>
                            <button
                              type="button"
                              className="quantity_minus"
                              onClick={() => {
                                handleQuantityDecrease(product.id);
                              }}
                            >
                              -
                            </button>
                          </div>
                        </td>
                        <td className="cart_price">
                          <p>{product.price}</p>
                        </td>
                        <td className="cart_btn">
                          <button type="button" className="btn pay">
                            결제
                          </button>
                          <button type="button" className="btn" onClick={() => handleDelete(product.id, product.size)}>
                            삭제
                          </button>
                        </td>
                      </tr>
                    ))}
                    <tr className="cart_arrive">
                      <td>도착 예정일 : {`${month + 1}월 ${date + 2}일 예정`}</td>
                      <td className="del_free">10만원 이상 구매 시 배송비 무료</td>
                    </tr>
                  </tbody>
                </table>
                <div className="check_delete">
                  <p>선택상품 총 {checkedItems.length} 개</p>
                  <button className="btn checked_del" onClick={handleCheckedDelete}>
                    삭제
                  </button>
                </div>
              </div>
            )}
            <div className="order">
              <h3 className="order_list">주문 내역</h3>
              <div className="product_price">
                <div className="">
                  {selectedProducts.map((product) => (
                    <p key={product.id}>{product.price} 원</p>
                  ))}
                </div>
                <div>
                  <div className="price_delivery">
                    <div className="order_price">
                      <p>상품 금액</p>
                      <p>{total.toString()} 원</p>
                    </div>
                    <div className="delivery">
                      <p>배송비</p>
                      <p>{priceDel >= 100000 ? "무료" : "3000"}</p>
                    </div>
                  </div>
                  <div className="total_price">
                    <p>총 결제 금액</p>
                    <p>{priceDel} 원</p>
                  </div>
                </div>
                <button type="button" className="detail_btn">
                  주문 결제<span></span>
                </button>
              </div>
            </div>
          </section>
          <section className="recomended">
            <h3>추천 상품</h3>
            <SlideItems />
          </section>
        </div>
      </div>
    </div>
  );
}

export default Cart;
