import { useEffect, useState } from "react";
import ShoesSize from "./ShoesSize";
import ClothesSize from "./ClothesSize";

function ProductSizeSelect({ selectedSizes, handleSizeChange }) {
  const [page, setPage] = useState("");

  useEffect(() => {
    const pathname = window.location.pathname;
    if (pathname.includes("shoes")) {
      setPage("shoes");
      console.log("신발");
    } else if (pathname.includes("clothes")) {
      setPage("clothes");
      console.log("옷");
    }
  });

  if (page === "shoes") {
    return <ShoesSize selectedSizes={selectedSizes} handleSizeChange={handleSizeChange} />;
  } else if (page === "clothes") {
    return <ClothesSize selectedSizes={selectedSizes} handleSizeChange={handleSizeChange} />;
  }
}

export default ProductSizeSelect;
