import { useState, React } from "react";
import { Link } from "react-router-dom";
import Aside from "../component/Aside";
import clothesData from "../Data/clothesData";

function Clothes() {
  const categories = ["top", "hood", "jacket", "pants", "sports"];
  const categoriesTitle = ["탑 & 티셔츠", "후디 & 크루", "재킷 & 베스트", "팬츠 & 타이츠", "스포츠 브라"];
  // 스타일 필터
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedBrands, setSelectedBrands] = useState([]);
  const [selectedColor, setSelectedColor] = useState([]);
  const [selectedPriceRange, setSelectedPriceRange] = useState([]);

  const handleCategoryChange = (style) => {
    if (selectedCategories.includes(style)) {
      setSelectedCategories(selectedCategories.filter((s) => s !== style));
    } else {
      setSelectedCategories([...selectedCategories, style]);
    }
  };

  const handleBrandChange = (brand) => {
    if (selectedBrands.includes(brand)) {
      setSelectedBrands(selectedBrands.filter((b) => b !== brand));
    } else {
      setSelectedBrands([...selectedBrands, brand]);
    }
  };

  const handleColorChange = (color) => {
    if (selectedColor.includes(color)) {
      setSelectedColor(selectedColor.filter((c) => c !== color));
    } else {
      setSelectedColor([...selectedColor, color]);
    }
  };

  const handlePriceRangeChange = (range) => {
    if (selectedPriceRange.includes(range)) {
      //기존의 배열과 중복이 된다면 삭제
      setSelectedPriceRange(selectedPriceRange.filter((r) => r !== range));
    } else {
      //기존의 있던 배열에 없던 값이면 추가
      setSelectedPriceRange([...selectedPriceRange, range]);
    }
  };

  const filteredProducts = clothesData.filter((clothes) => {
    const hasSelectedBrand = selectedBrands.length === 0 || selectedBrands.includes(clothes.brand);
    const hasSelectedCategory = selectedCategories.length === 0 || selectedCategories.includes(clothes.style);
    const hasSelectedColor = selectedColor.length === 0 || selectedColor.includes(clothes.color);
    const hasSelectedPriceRange =
      selectedPriceRange.length === 0 ||
      selectedPriceRange.some((range) => {
        const [min, max] = range.split("-");
        const price = parseInt(clothes.price.replace(/,/g, "")); // 쉼표(,)를 제거하고 숫자로 변환
        return price >= parseInt(min) && price <= parseInt(max);
      });
    return hasSelectedBrand && hasSelectedCategory && hasSelectedColor && hasSelectedPriceRange;
  });

  // 필터 유무
  const [filterBtn, setFilterBtn] = useState("block");
  const filterClick = () => {
    if (filterBtn === "block") {
      setFilterBtn("none");
    } else {
      setFilterBtn("block");
    }
  };

  // 필터의 유무에 따른 사이즈 변경
  const listWrapperStyles = {
    width: filterBtn === "none" ? "100%" : "1551px",
  };

  return (
    <div className="inner shop_inner">
      <div className="filter">
        <h2>신발</h2>
        <span className="filter_btn" onClick={filterClick}>
          필터 숨기기
        </span>
      </div>
      <div className="side_list">
        <Aside
          filterBtn={filterBtn}
          selectedCategories={selectedCategories}
          handleCategoryChange={handleCategoryChange}
          selectedBrands={selectedBrands}
          handleBrandChange={handleBrandChange}
          selectedColor={selectedColor}
          handleColorChange={handleColorChange}
          selectedPriceRange={selectedPriceRange}
          handlePriceRangeChange={handlePriceRangeChange}
          categories={categories}
          categoriesTitle={categoriesTitle}
        />
        <div className="list_wrapper" style={listWrapperStyles}>
          {filteredProducts.map(function (a, i) {
            return (
              <div className="list" key={i}>
                <Link to={`/clothes/detail/${filteredProducts[i].id}`}>
                  <div className="img_wrapper">
                    <img src={process.env.PUBLIC_URL + "/img/" + filteredProducts[i].src}></img>
                  </div>
                  <div className="name_price">
                    <h4 className="shoes_name">{filteredProducts[i].name}</h4>
                    <p className="price">{filteredProducts[i].price}</p>
                  </div>
                  <p className="category">{filteredProducts[i].category}</p>
                </Link>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default Clothes;
