import { useState, useEffect } from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import Header from "./component/Header.js";
import Footer from "./component/Footer.js";
import Jordan from "./pages/Jordan.js";
import Shoes from "./pages/Shoes.js";
import Detail from "./pages/Detail.js";
import Clothes from "./pages/Clothes.js";
import MainPage from "./pages/MainPage.js";
import shoesData from "./Data/shoesData.js";
import clothesData from "./Data/clothesData.js";
import Cart from "./pages/Cart.js";
import "./App.css";

function App() {
  const [selectedSizes, setSelectedSizes] = useState([]);
  const getSelectedProducts = () => {
    return shoesData.filter((item) => selectedSizes.includes(item.size)) && clothesData.filter((item) => selectedSizes.includes(item.size));
  };

  const location = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className="App">
      <Header />
      <Routes>
        <Route path="/" element={<MainPage />} />
        <Route path="/jordan" element={<Jordan />} />
        <Route path="/shoes" element={<Shoes filteredProducts={shoesData} />} />
        <Route path="/shoes/detail/:productId" element={<Detail filteredProducts={shoesData} setSelectedSizes={setSelectedSizes} />} />
        <Route path="/clothes" element={<Clothes filteredProducts={clothesData} />} />
        <Route path="/clothes/detail/:productId" element={<Detail filteredProducts={clothesData} setSelectedSizes={setSelectedSizes} />} />
        <Route path="/cart" element={<Cart selectedProducts={getSelectedProducts()} />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
