import ProductSizeSelect from "./ProductSizeSelect";

function Order({ page, product, selectedSizes, handleClick, handleSizeChange }) {
  return (
    <div className="order">
      <div className="txt_wrapper">
        <h3 className="shoes_name">{product.name}</h3>
        <p className="shoes_category">{product.category}</p>
        <p className="shoes_price">{product.price}</p>
      </div>
      <div className="shoes_size">
        <div className="size_title">
          <h4>사이즈 선택</h4>
          <p>사이즈 가이드</p>
        </div>
        <ProductSizeSelect selectedSizes={selectedSizes} handleSizeChange={handleSizeChange} page={page} />
      </div>
      <div>
        <button tpye="button" className="detail_btn" onClick={handleClick}>
          장바구니<span></span>
        </button>
        <button type="button" className="detail_btn_2">
          위시리스트<span></span>
        </button>
      </div>
      <div className="detail_description">
        <p className="desc">{product.desc}</p>
        <ul className="color_code">
          <li>현재컬러 : {product.detail_color}</li>
          <li>스타일 번호 : {product.code}</li>
        </ul>
      </div>
    </div>
  );
}

export default Order;
