function Aside({ filterBtn, selectedCategories, handleCategoryChange, selectedBrands, handleBrandChange, selectedColor, handleColorChange, selectedPriceRange, handlePriceRangeChange, categories, categoriesTitle }) {
  return (
    <div className="aside" style={{ display: filterBtn }}>
      <div>
        <h3>스타일</h3>
        <ul className="product_style">
          <li>
            <input type="checkbox" id="life" checked={selectedCategories.includes(categories[0])} onChange={() => handleCategoryChange(categories[0])}></input>
            <label for="life">{categoriesTitle[0]}</label>
          </li>
          <li>
            <input type="checkbox" id="jordan" checked={selectedCategories.includes(categories[1])} onChange={() => handleCategoryChange(categories[1])}></input>
            <label for="jordan">{categoriesTitle[1]}</label>
          </li>
          <li>
            <input type="checkbox" id="running" checked={selectedCategories.includes(categories[2])} onChange={() => handleCategoryChange(categories[2])}></input>
            <label for="running">{categoriesTitle[2]}</label>
          </li>
          <li>
            <input type="checkbox" id="training" checked={selectedCategories.includes(categories[3])} onChange={() => handleCategoryChange(categories[3])}></input>
            <label for="training">{categoriesTitle[3]}</label>
          </li>
          <li>
            <input type="checkbox" id="sandle" checked={selectedCategories.includes(categories[4])} onChange={() => handleCategoryChange(categories[4])}></input>
            <label for="sandle">{categoriesTitle[4]}</label>
          </li>
        </ul>
      </div>
      <div className="product_brand">
        <h3>브랜드</h3>
        <ul>
          <li>
            <input id="nike" type="checkbox" checked={selectedBrands.includes("nike")} onChange={() => handleBrandChange("nike")} />
            <label for="nike">나이키 스포츠웨어</label>
          </li>
          <li>
            <input id="lab" type="checkbox" checked={selectedBrands.includes("lab")} onChange={() => handleBrandChange("lab")} />
            <label for="lab">NikeLab</label>
          </li>
          <li>
            <input id="acg" type="checkbox" checked={selectedBrands.includes("acg")} onChange={() => handleBrandChange("acg")} />
            <label for="acg">ACG</label>
          </li>
        </ul>
      </div>
      <div className="product_color">
        <h3>색상</h3>
        <ul className="color_list">
          <li>
            <input id="white" className="white" type="checkbox" checked={selectedColor.includes("white")} onChange={() => handleColorChange("white")} />
            <label for="white">화이트</label>
          </li>
          <li>
            <input id="black" className="black" type="checkbox" checked={selectedColor.includes("black")} onChange={() => handleColorChange("black")} />
            <label for="black">블랙</label>
          </li>
          <li>
            <input id="blue" className="blue" type="checkbox" checked={selectedColor.includes("blue")} onChange={() => handleColorChange("blue")} />
            <label for="blue">블루</label>
          </li>
          <li>
            <input id="red" className="red" type="checkbox" checked={selectedColor.includes("red")} onChange={() => handleColorChange("red")} />
            <label for="red">레드</label>
          </li>
        </ul>
      </div>
      <div className="product_price">
        <h3>가격</h3>
        <ul>
          <li>
            <input type="checkbox" id="price_one" value="0-50000" checked={selectedPriceRange.includes("0-50000")} onChange={() => handlePriceRangeChange("0-50000")} />
            <label for="price_one">0 - 50,000원</label>
          </li>
          <li>
            <input type="checkbox" id="price_two" value="50000-100000" checked={selectedPriceRange.includes("50000-100000")} onChange={() => handlePriceRangeChange("50000-100000")} />
            <label for="price_two">50,000 - 100,000원</label>
          </li>
          <li>
            <input type="checkbox" id="price_three" value="100000-150000" checked={selectedPriceRange.includes("100000-150000")} onChange={() => handlePriceRangeChange("100000-150000")} />
            <label for="price_three">100,000 - 150,000원</label>
          </li>
          <li>
            <input type="checkbox" id="price_four" value="150000-200000" checked={selectedPriceRange.includes("150000-200000")} onChange={() => handlePriceRangeChange("150000-200000")} />
            <label for="price_four">150,000 - 200,000원</label>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Aside;
