import { useState } from "react";
import bestData from "../Data/bestData.js";
import shoesData from "../Data/shoesData.js";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import { Link } from "react-router-dom";
// style
import "swiper/css";
import "swiper/css/navigation";

function SlideItems() {
  return (
    <Swiper navigation={true} modules={[Navigation]} spaceBetween={24} slidesPerView={4} loop={true} className="featured_swiper">
      {shoesData.map(function (a, i) {
        return (
          <SwiperSlide className="swiper_slide" key={i}>
            <Link to={`/shoes/detail/${shoesData[i].id}`}>
              <div className="img_wrapper">
                <img src={process.env.PUBLIC_URL + "/img/" + shoesData[i].src}></img>
              </div>
              <div className="name_price">
                <h4 className="shoes_name">{shoesData[i].name}</h4>
                <p className="price">{shoesData[i].price}</p>
              </div>
              <p className="category">{shoesData[i].category}</p>
            </Link>
          </SwiperSlide>
        );
      })}
    </Swiper>
  );
}

export default SlideItems;
