function ShoesSize({ selectedSizes, handleSizeChange }) {
  const handleCheckboxChange = (size) => {
    if (selectedSizes === size) {
      handleSizeChange(null);
    } else {
      handleSizeChange(size);
    }
  };
  return (
    <div>
      <ul className="size_select">
        <li>
          <input id="size_list_225" type="checkbox" checked={selectedSizes.includes("225")} onChange={() => handleSizeChange("225")}></input>
          <label for="size_list_225">225</label>
        </li>
        <li>
          <input id="size_list_230" type="checkbox" checked={selectedSizes.includes("230")} onChange={() => handleSizeChange("230")}></input>
          <label for="size_list_230">230</label>
        </li>
        <li>
          <input id="size_list_235" type="checkbox" checked={selectedSizes.includes("235")} onChange={() => handleSizeChange("235")}></input>
          <label for="size_list_235">235</label>
        </li>
        <li>
          <input id="size_list_240" type="checkbox" checked={selectedSizes.includes("240")} onChange={() => handleSizeChange("240")}></input>
          <label for="size_list_240">240</label>
        </li>
        <li>
          <input id="size_list_245" type="checkbox" checked={selectedSizes.includes("245")} onChange={() => handleSizeChange("245")}></input>
          <label for="size_list_245">245</label>
        </li>
        <li>
          <input id="size_list_250" type="checkbox" checked={selectedSizes.includes("250")} onChange={() => handleSizeChange("250")}></input>
          <label for="size_list_250">250</label>
        </li>
        <li>
          <input id="size_list_255" type="checkbox" checked={selectedSizes.includes("255")} onChange={() => handleSizeChange("255")}></input>
          <label for="size_list_255">255</label>
        </li>
        <li>
          <input id="size_list_260" type="checkbox" checked={selectedSizes.includes("260")} onChange={() => handleSizeChange("260")}></input>
          <label for="size_list_260">260</label>
        </li>
        <li>
          <input id="size_list_265" type="checkbox" checked={selectedSizes.includes("265")} onChange={() => handleSizeChange("265")}></input>
          <label for="size_list_265">265</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_270" type="checkbox" checked={selectedSizes.includes("270")} onChange={() => handleSizeChange("270")}></input>
          <label for="size_list_270">270</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_275" type="checkbox" checked={selectedSizes.includes("275")} onChange={() => handleSizeChange("275")}></input>
          <label for="size_list_275">275</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_280" type="checkbox" checked={selectedSizes.includes("280")} onChange={() => handleSizeChange("280")}></input>
          <label for="size_list_280">280</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_285" type="checkbox" checked={selectedSizes.includes("285")} onChange={() => handleSizeChange("285")}></input>
          <label for="size_list_285">285</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_290" type="checkbox" checked={selectedSizes.includes("290")} onChange={() => handleSizeChange("290")}></input>
          <label for="size_list_290">290</label>
        </li>
        <li className="margin_zero">
          <input id="size_list_295" type="checkbox" checked={selectedSizes.includes("295")} onChange={() => handleSizeChange("295")}></input>
          <label for="size_list_295">295</label>
        </li>
      </ul>
    </div>
  );
}

export default ShoesSize;
